import { Component, OnInit, ViewChild } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  FormArray,
  Validators,
  RequiredValidator,
  AbstractControl,
  FormControl
} from "@angular/forms";
import { HttpService } from "../../services/http.service";
import { NotificationsService } from "../../notifications/notifications.service";
import { HttpResponse } from '@angular/common/http';
import { CustomValidators } from 'ng4-validators';

declare var $;
declare var swal;

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css']
})
export class AddComponent implements OnInit {
  hide = true;
  userForm: FormGroup;
  errorPassword: any;
  errorPasswordLength: string;
  errorEmail: any;
  errorMessage = false;
  emailPattern: any = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/; 

  @ViewChild('f') myNgForm;

  constructor(
    private fb: FormBuilder,
    private http: HttpService,
    private alert: NotificationsService
  ) { }

  ngOnInit() {
    
    let password = new FormControl('', Validators.required);
    let confirmPassword = new FormControl('',[Validators.required, CustomValidators.equalTo(password)]);
    // init userForm
    this.userForm = this.fb.group({
      name: ['', Validators.required],
      email: ['', [Validators.required, Validators.pattern(this.emailPattern), Validators.email]],
      password: password,
      confirmPassword: confirmPassword
    })

} 
resetForm() {
  this.myNgForm.resetForm();
}
  onSubmit() {
    const fd: FormData = new FormData();
    let formInput = this.userForm.value;
    delete formInput.confirmPassword;
    
    fd.append("json", JSON.stringify(formInput)); 
    this.http.postFormData('create/user', fd).subscribe(httpResponse => {
      if(httpResponse.status === 201) {
        this.alert.showNotification('Uspesno ste dodali novog korisnika', 'success', '');
        // this.userForm.reset();
        this.resetForm();

        // this.userForm.markAsPristine();
        // this.userForm.markAsUntouched();
        // this.userForm.updateValueAndValidity();
      }
    },
    error => {
      if(error.status == 422){
        if(error.json().password != null) {
          // set password as invalid and trigger error
          this.userForm.controls.password.setErrors({'incorrect': true});
          this.errorPasswordLength = error.json().password[0];
        } else {
          this.errorPasswordLength = '';
        }
        if(error.json().email != null) {
            // set password as invalid and trigger error
            this.userForm.controls.email.setErrors({'incorrect': true});
            this.errorEmail = error.json().email[0];
          }
        else {
          this.errorEmail = '';
        }
        if(this.errorPasswordLength != ''){
          this.alert.showNotification('Greska uneta sifra treba da bude duza od 8 karatera', 'danger', 'notifications');
        }
        if(this.errorEmail != '') {
          this.alert.showNotification('email adresa '+ this.userForm.controls.email.value + ' vec postoji molimo izaberite drugu', 'info', 'notifications');
        }
      }
    })
  }
}



