import { Component, OnInit, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { SelectionModel, DataSource } from "@angular/cdk/collections";
import { MatPaginator, MatTableDataSource } from "@angular/material";
import {
  debounceTime,
  distinctUntilChanged,
  startWith,
  tap,
  delay
} from "rxjs/operators";

import { UserDataSource } from "../services/user.datasource";
import { HttpService } from "../services/http.service";
import { NotificationsService } from "../notifications/notifications.service";

declare var swal: any;
declare var $: any;

@Component({
  selector: "app-users",
  templateUrl: "users.component.html",
  styleUrls: ["./users.component.css"]
})
export class UsersComponent implements OnInit {
  total;
  pageSize;

  dataSource: UserDataSource;
  displayedColumns = ["id", "name", "actions"];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  constructor(
      private router: Router,
      private http: HttpService,
      private alert: NotificationsService
    ) {}

  ngOnInit() {
    this.dataSource = new UserDataSource(this.http);
    this.dataSource.loadUser(1);

    this.http.get('get/user', 1).subscribe(httpResponse => {
        if(httpResponse.status === 200) {
            this.total = httpResponse.json().total;
            this.pageSize = httpResponse.json().per_page;
        }
    }, error => {
      this.alert.showNotification('Greska na serveru, molimo pokusajte kasnije!', 'danger', '');
    })
  }
  ngAfterViewInit() {
    this.paginator.page.pipe(tap(() => this.loadUserPage())).subscribe();
  }
  loadUserPage() {
    this.dataSource.loadUser(this.paginator.pageIndex + 1);
    console.log("loadUserPage triggered!: ", this.paginator.pageIndex);
  }

  OnEditUser(id) {
    this.router.navigate(["users/edit/", id]);
  }

  OnAddUser() {
    this.router.navigate(["users/add"]);
  }

  OnDeleteUser(id, name) {
    swal({
      title: `Da li ste sigurni da elite da obriete korisnika ${name} ?`,
      text: "Podatke nije mogue povratiti nakon brisanja!",
      type: "warning",
      showCancelButton: true,
      confirmButtonClass: "btn btn-success",
      cancelButtonClass: "btn btn-danger",
      confirmButtonText: "Yes, delete it!",
      buttonsStyling: false
    }).then(() => {
      this.http.delete("delete/user/" + id).subscribe(httpResponse => {
        if (httpResponse.status === 204) {
          swal({
            title: "Obrisano!",
            text: `Korisnik ${name} je uspesno obrisan!.`,
            type: "success",
            confirmButtonClass: "btn btn-success",
            buttonsStyling: false
          });
          this.http.get("get/user", 1).subscribe(data => {
            this.paginator.length = data.json().total;
          });
          this.dataSource = new UserDataSource(this.http);
          this.dataSource.loadUser(this.paginator.pageIndex);
        }
        error => {
          this.alert.showNotification(
            "Greska korisnik" + name + "nije obrisan!",
            "danger",
            "error"
          );
        };
      });
    });
  }
}



